@import url(https://fonts.googleapis.com/css?family=Orbitron:400,900);
::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-track {
  background: #000;
  border-left: 3px solid #000;
  border-right: 3px solid #000;
}
::-webkit-scrollbar-thumb {
  background: rgba(176,196,222,1);
  border-left: 4px solid #000;
  border-right: 4px solid #000;
}
* {
  position: relative;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}

body,html {
  background: url(https://img2.cgtrader.com/items/671249/905a8ffe7a/star-field-space-background-3d-model.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: sans-serif;
  font-size: 15px;
  line-height: 1.6em;
  overflow: hidden;
}
body:after,
html:after {
  background: url(https://i.ytimg.com/vi/fOl6TUPTcO8/maxresdefault.jpg);
  width: 100%;
  height: 100%;
  opacity: 0.8;
}

h1,h2,h3,h4,h5,h6,em,strong {
  font-family: 'Orbitron', monospace, sans-serif;
  letter-spacing: 1em;
  font-size: 1.5em;
}
p, blockquote{
  font-family: Arial, sans-serif;
}
ul{
  list-style: none;
  margin: 0;
  padding: 0;
}
a:link,
a:visited,
a:focus,
a:active,
a:hover,
a {
  text-decoration: none;
  font-size: medium;
  font-size: initial;
  font-style: normal;
  font-weight: normal;
  letter-spacing: normal;
  color: inherit; }

a:hover {
  color: #FF0000; }

.react-tabs__tab-list {
  width: 100%;
  margin: 0;
  padding: 0;
}
.react-tabs__tab {
  color: rgba(255,215,0,1);
  text-align: center;
  display: inline;
  position: relative;
  list-style: none;
  cursor: pointer;
}
.react-tabs__tab:hover{
  font-weight: bolder;
  text-shadow: 0px 0px 4px black, 7px 0px 5px rgba(176,196,222,1), -7px 0px 5px rgba(176,196,222,1);
}
.react-tabs__tab--selected {
  color: rgba(176,196,222,1);
  display: block;
  font-weight: bolder;
  text-shadow: 0px 0px 4px black, 7px 0px 5px rgba(176,196,222,1), -7px 0px 5px rgba(176,196,222,1);
}
.react-tabs__tab--selected:hover{
  text-shadow: 0px 0px 4px black, 7px 0px 5px rgba(176,196,222,1), -7px 0px 5px rgba(176,196,222,1);
  font-weight: bolder;
}
.react-tabs__tab-panel {
  display: none;
}
.react-tabs__tab-panel--selected {
  clear: both;
  display: block;
}
.react-tabs__tab--disabled {
  color: GrayText;
  cursor: default;
}
.react-tabs__tab:focus {
  box-shadow: 0 0 5px hsl(208, 99%, 50%);
  border-color: hsl(208, 99%, 50%);
  outline: none;
}
.react-tabs__tab:focus:after {
  content: "";
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: #fff;
}
/* --- General -- */
.tabs{
  background: black;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border-top: 2px solid rgba(255,215,0,1);
  border-bottom: 2px solid rgba(255,215,0,1);
  height: 6vh;
}
.react-tabs__tab-panel--selected>div{
  background: rgba(0,0,0,.5);
  height: 84vh;
  margin: 0 auto;
  overflow: auto;
  padding: 0;
  color: rgba(176,196,222,1);
}
@media only screen and (max-width: 480px){
  .react-tabs__tab{
    width: 25%;
    margin: 0;
  }
}

